.modal-container {
    position: relative;
    padding: 2rem;
    background-color: #FDE8AF;
    border: 0.7rem inset #FFBE1A;
    width: 70rem;
    height: 25rem;
    border-radius: 1.5rem;
}

.modal-container.true {
    display: block;
    position: absolute;
}

.modal-container.false {
    display: none;
}

.pharaoh {
    background-image: url(../../images/faraon.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    height: 19rem;
    width: 25%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-9%, 6%);
}

.modal-body {
    position: absolute;
    top: 7%;
    left: 16%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 2.7rem;
    line-height: 2.2rem;
    text-align: justify;
    padding: 1rem;
    margin: 2rem;
    height: 18rem;
}

/* Diseño Responsive */
@media (max-width: 768px) {
    .modal-container{
        max-width: 85%;
        height: initial;
    }
    .pharaoh{
        display: none;
    }
    .modal-body{
        position: initial;
        top: 0;
        left: 0;
        margin: 1rem;
        padding: 0;
        height: initial;
    }
  }