.beetle{
    background-image: url(../../images/escarabajo.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 4rem;
    height: 4rem;
    margin: 0 1rem;
}

.menu-container .beetle{
    width: 15rem;
    height: 15rem;
    margin-bottom: 11rem;
    -webkit-animation: mover 2s infinite  alternate;
    animation: mover 2s infinite  alternate;
}

.modal-container .beetle{
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    width: 7rem;
    height: 7rem;
}

@-webkit-keyframes mover {
    0% { transform: translateY(8rem); }
    100% { transform: translateY(-5rem); }
}
@keyframes mover {
    0% { transform: translateY(8rem); }
    100% { transform: translateY(-5rem); }
}

/* Diseño Responsive */
@media (max-width: 576px) {
    .beetle{
        width: 3.5rem;
        height: 3.5rem;
        margin: 0 0.5rem;
    }
    .menu-container .beetle {
        width: 10rem;
        height: 10rem;
    }
}